* {
  padding: 0;
  margin: 0;
  list-style: none;
  outline: none;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: #191919;
}

html, body {
  min-height: 100%;
  min-width: 100%;
}

a,
span,
p,
b,
h1,
h2,
h3,
h4,
h5 {
  color: #191919;
}

h1 {
