.preview__top .header {
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.5);
  padding: 0px 60px;
}
.preview__top .header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.preview__top .header__top .right {
  display: flex;
  align-items: center;
  gap: 60px;
}
.preview__top .header__top .right .header__btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  gap: 15px;
  border-radius: 40px;
  border: none;
  cursor: pointer;
}
.preview__top .header__top .right .header__btn > p {
  font-weight: 600;
  color: #191919;
}
.preview__top .header__top .right > span {
  color: #312EE9;
  cursor: pointer;
}
@media (max-width: 1280px) {
  .preview__top .header__top .right {
    position: fixed;
    right: -100%;
    top: 479px;
    flex-direction: column;
    gap: 20px;
    padding: 50px 45px;
  }
  .preview__top .header__top .right.active {
    right: 0;
    background: rgba(255, 255, 255, 0.5);
    z-index: 200;
    transition: 0.3s;
  }
}
@media (max-width: 800px) {
  .preview__top .header__top .left > a > img {
    width: 80%;
  }
}
.preview__top .header__top .burger {
  display: none;
  cursor: pointer;
}
.preview__top .header__top .burger span {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3 ease-in-out;
  background-color: #191919;
}
@media (max-width: 1280px) {
  .preview__top .header__top .burger span.active:nth-child(2) {
    opacity: 0;
  }
  .preview__top .header__top .burger span.active:nth-child(1) {
    transform: translate(0, 8px) rotate(45deg);
  }
  .preview__top .header__top .burger span.active:nth-child(3) {
    transform: translate(0, -8px) rotate(-45deg);
  }
}
@media (max-width: 1280px) {
  .preview__top .header__top .burger {
    display: block;
  }
}
.preview__top .header__bottom nav > ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.preview__top .header__bottom nav > ul > li {
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
  padding: 20px;
}
.preview__top .header__bottom nav > ul > li > a, .preview__top .header__bottom nav > ul > li span {
  cursor: pointer;
  color: #191919;
  font-weight: 600;
}
.preview__top .header__bottom nav > ul > li > .dropDown {
  display: none;
  min-width: 270px;
  z-index: 999;
  position: absolute;
  top: 59px;
  left: -20px;
  flex-direction: column;
  align-items: center;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}
.preview__top .header__bottom nav > ul > li > .dropDown > li {
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  min-width: 100%;
  padding: 13px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
}
.preview__top .header__bottom nav > ul > li > .dropDown > li:hover {
  background-color: #E8E8F0;
}
