  display: flex;
  align-items: center;
  gap: 10px;
}
.preview__top .header__bottom nav > ul > li > .dropDown > li > a > span {
  font-size: 14px;
  font-weight: 500;
}
.preview__top .header__bottom nav > ul > li > .dropDown > li:last-child {
  border-bottom: none;
}
.preview__top .header__bottom nav > ul > li > .dropDown.active {
  display: flex;
}
.preview__top .header__bottom nav > ul > li > .dropDown:last-child {
  left: -100px;
}
.preview__top .header__bottom nav > ul > li:hover > .dropDown {
  display: flex;
}
.preview__top .header__bottom nav.black ul li a, .preview__top .header__bottom nav.black ul li span {
