.burger__wrapper {
  position: fixed;
  right: -100%;
  top: 0;
  background-color: #E8E8F0;
  z-index: 999;
  width: 340px;
  height: 100vh;
  transition: all 0.3s ease-in-out;
}
.burger__wrapper.active {
  right: 0%;
  overflow-y: scroll;
}
.burger__wrapper .close {
  display: flex;
  justify-content: flex-end;
  padding: 20px 20px 0;
  margin-top: 20px;
  margin-right: 40px;
}
.burger__wrapper .close-menu > span {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3 ease-in-out;
  background-color: #191919;
}
@media (max-width: 1280px) {
  .burger__wrapper .close-menu > span.active:nth-child(2) {
    opacity: 0;
  }
  .burger__wrapper .close-menu > span.active:nth-child(1) {
    transform: translate(0, 8px) rotate(45deg);
  }
  .burger__wrapper .close-menu > span.active:nth-child(3) {
    transform: translate(0, -8px) rotate(-45deg);
  }
}
.burger__wrapper .close > p {
  font-size: 40px;
  font-weight: 700;
}
.burger__wrapper > ul {
  max-width: 100%;
}
.burger__wrapper > ul > li {
  padding: 20px;
  border-bottom: 1px solid #707086;
}
.burger__wrapper > ul > li > span, .burger__wrapper > ul > li a {
  font-weight: 600;
}
.burger__wrapper > ul > li > p {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}
.burger__wrapper > ul > li > p > span {
  font-weight: 600;
}
.burger__wrapper > ul > li > ul {
  display: none;
}
.burger__wrapper > ul > li > ul.active {
  display: block;
  top: 0;
  left: 0;
}
.burger__wrapper > ul > li > ul > li > a {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}
.burger__wrapper > ul > li > ul > li > a > span {
  font-weight: 600;
}
.burger__wrapper .btn-sector {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 20px;
}
